import { useContext, useEffect, useState, useRef, createRef } from "react";
import { InitContext } from "../../contexts/initContext";
import { useTranslation } from "react-i18next";
import { v4 as uuidv4 } from 'uuid';
import { FaCheck } from "react-icons/fa";

import PriceCard from "../website/PriceCard";
import "../../styles/ServicePackage.scss"

function ServicePackage(props) {
    const { 
        TransactionServicepackage,
        plans
    } = useContext(InitContext);
    const { t, i18n } = useTranslation();
    const [selectedPlan, setSelectedPlan]=useState(null)
    const [isPriceCardDiv, setIsPriceCardDiv] = useState(true) 
    const [isSelectedCardDiv, setIsSelectedCardDiv] = useState(false)
    // const PriceCardard0 = useRef()
    // const PriceCardard1 = useRef()
    // const PriceCardard2 = useRef()
    // const PriceCardard3 = useRef()
    // const PriceCardard4 = useRef()

    const clickPlanBtn = (e, plan) =>{
        console.log(e)
        console.log(plan)

        setSelectedPlan(plan)
        // setIsPriceCardDiv(false)
    }

    // function clickPlanBtn(params) {
    //     console.log(params)
    //     console.log(this)
    // }

    useEffect(()=>{
        fetch(TransactionServicepackage, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                "action": "Query",
                "packageid": "",
                "packagetype": "",    
                "enabled": "",
            })
        })
        .then(res=>res.json())
        .then(data=> {
            console.log(data)
        })
        .catch(err => console.error(err))
    }, [TransactionServicepackage])

    return(
        <div className="ServicePackage">
            <section>
                <h1>1. {t("choose_one_plan")}</h1>
                <div className="price-card-div">
                    { isPriceCardDiv &&
                        plans.map((plan, i)=>{
                            // const refName = PriceCard${i}


                            return <div key={uuidv4()} className={plan.packageid===props.thisPackage.packageid ? "thisPackage" : ""}>
                                <PriceCard 
                                    t={t} 
                                    plan={plan} 
                                    linkto={"#"} 
                                    linkText={plan.packageid===props.thisPackage.packageid ? t("your_package") : t("upgrade")} 
                                    i18n={i18n} 
                                    onClick={(e)=>clickPlanBtn(e, plan)} 
                     
                                    />
                            </div>
                        })
                    }
                </div>                
            </section>
            <section>
                <h1>{t("plan_details")}</h1>
                {
                    selectedPlan!==null && 
                    <div>
                        <h1>{t("plan_details")}</h1>
                        <h1>{selectedPlan.packageid}</h1>
                        <h1><span>{t("selected_plan")}</span>{i18n.language==="en" ? selectedPlan.packagename_en : selectedPlan.packagename_cht}</h1>
                        <h1>{selectedPlan.price}</h1>
                    </div>
                }
                </section>        

                
        </div>
    )
}

export default ServicePackage;